















































































































































































input[type="text"] {
  background: #fff;
  border: 1px solid #d4d4d4;
}

.checkContainer {
  position: relative;
}

.checkContainer span {
  width: 30px;
  position: absolute;
  left: 0;
  height: 30px;
  border-radius: 8px;
  border: 4px solid #d4d4d4;
  display: block;
  background: #fff;
}

input[type="checkbox"] {
  width: 30px;
  height: 30px;
  opacity: 0;
  border-radius: 8px;
  position: relative;
  z-index: 999;
}

.checkContainer input:checked + span {
  background: #66cdb6;
}
