



























































.panelContainer__left div a {
  color: #787878;
}


.modal {
  color: #787878;
}
.plan-list__content {
  width: 100%;
  margin: 0;
  display: grid !important;
  grid-template-columns: 1fr 1fr 1fr 1fr !important;
}
.modal {
  height: auto !important;
}

.modalContent {
  text-align: center;
}

.modalBtn {
  display: inline-block;
  padding: 0 24px;
  margin: 0 12px;
  color: #fff;
}

.content {
  padding: 34px 58px !important;
}

.cancelBtn {
  background: #66cdb6;
  padding: 0;
  width: 168px;
  height: 52px;
  line-height: 52px;
  border-radius: 4px;
  text-align: center !important;
  font-size: 24px;
}

.confirmBtn {
  padding: 0;
  width: 168px;
  height: 52px;
  line-height: 52px;
  border-radius: 4px;
  text-align: center !important;
  background: #bcbcbc;
  font-size: 24px;
}
  
