}

.teamClass {
  display: flex;
  width: 400px;
  justify-content: space-between;
  &__block {
    display: flex;
    align-items: center;
    input[type="checkbox"] {
      position: relative !important;
      left: auto;
      -webkit-appearance: checkbox;
    }

    button {
      background: #ff8a8a;
      padding: 4px 23px;
      border-radius: 4px;
      color: #fff;
      margin-left: 16px;
    }
  }
}

label {
  padding: 0 !important;
  font-size: 1rem !important;
  font-weight: normal !important;
  &:before {
    display: none;
  }
  &:after {
    display: none;
  }
}

input[type="radio"] .planJob {
  margin: 10px auto;
  img {
    vertical-align: bottom;
    margin-left: 20px;
    margin-bottom: 4px;
  }
  .addBtn {
    display: inline-block;
    float: right;
  }
}

select {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 12px;
  border-radius: 4px;
  margin: 0 4px;
}

p {
  color: #777;
}

.twoColumn {
  grid-template-columns: 1fr 1fr;
}

input[type="radio"]:checked::before {
  background: #66cdb6;
}

.styleBlock__text {
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: auto;
  color: #777;
  input[type="radio"] {
    display: inline-block;
    left: unset;
    position: relative;
    width: 30px;
    height: 30px;
    border-radius: 50em;
    -webkit-appearance: auto;
    margin-right: 8px;
    top: 0 !important;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
      border-radius: 50em;
      border: 4px solid #d4d4d4;
    }
  }
}

.addBtn {
  float: right;
  margin-bottom: 10px;
}

select {
  color: #858585;
}
