}

.styleList {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  grid-gap: 20px;
  padding-bottom: 10px;
  &__left {
    display: flex;
    justify-content: space-between;
    border-right: 3px solid #d4d4d4;
    padding-right: 8px;
    margin-right: 8px;
  }
  &__right {
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1);
    & .styleBlock {
      box-shadow: none;
    }
  }
}

.styleBlock {
  display: flex;
  align-items: center;
  flex-direction: column;
  background: #fff;
  padding: 18px 36px;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1);
  &__right {
    background: #fff;
  }
  &__text {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 182px;
    margin-bottom: 2px;
    color: #777;
  }
  img {
    margin: 6px auto;
  }
  &__btn {
    border: 1px solid #d4d4d4;
    border-radius: 4px;
    width: 86px;
    height: 32px;
    line-height: 32px;
    font-size: 16px;
    padding: 0;
    text-align: center;
    margin-top: 12px;
    color: #777;
  }
}

input[type="radio"] {
  display: inline-block;
  left: unset;
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50em;
  -webkit-appearance: auto;
  margin-right: 8px;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    border-radius: 50em;
    border: 4px solid #d4d4d4;
  }
}
input[type="radio"]:checked::before {
  background: #66cdb6;
}

.styleBlock__text input[type="radio"] {
  top: 0 !important;
}

.deleteBtn {
  width: 50px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  background: #f45454;
  color: #fff;
  border-radius: 4px;
}

.uploadBlock {
  width: 50%;
  display: inline-block;
  color: #777;
  align-self: stretch;
  position: relative;
  padding: 30px 0 30px 30px;
  height: 100%;
  &::before {
    content: "";
    height: 348px;
    width: 6px;
    background: #eaeaea;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}

.styleList__right {
  background: #fff;
}
