}

.planTitle__text {
  width: 200px;
}

.planJob {
  margin: 30px auto;
  img {
    vertical-align: bottom;
    margin-left: 20px;
    margin-bottom: 4px;
  }
  .addBtn {
    display: inline-block;
    float: right;
  }
}

label {
  padding: 0 !important;
  font-size: 1rem !important;
  font-weight: normal !important;
  &:before {
    display: none;
  }
  &:after {
    display: none;
  }
}

.teamClass__block input[type="checkbox"] {
  position: relative !important;
  left: auto;
  -webkit-appearance: checkbox;
}

select {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 12px;
  border-radius: 4px;
}

.flexRow {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

input[type="radio"] {
  display: inline-block;
  left: unset;
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50em;
  -webkit-appearance: auto;
  margin-right: 8px;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    border-radius: 50em;
    border: 4px solid #d4d4d4;
  }
  &:checked::before {
    background: #66cdb6;
  }
}

.styleBlock__text input[type="radio"] {
  top: 0 !important;
}

.styleBlock__text {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 182px;
  margin-bottom: 2px;
  color: #777;
}

input[type="checkbox"] {
  appearance: auto;
  width: 20px;
  height: 20px;
  margin: 0 5px 0 10px;
}

.dispatched {
  text-align: center;
  background: #f45454;
  color: #fff;
  border-radius: 10px;
}

