














































































































































































































































































.planTopic .themeColor {
  margin: 23px 0;
}

.addBtn {
  position: absolute;
  right: 0;
}

table.smallTable {
  width: 70%;
}

.teamClass__block input + label {
  display: block;
  cursor: pointer;
  position: absolute;
  top: 2px;
  left: 15px;
  border-radius: 100px;
  width: 24px;
  height: 24px;
  background: #fff;
  border: 3px solid lightgray;
}

.teamClass__block input + label::before {
  display: block;
  content: "";
  text-align: center;
  font-size: 16px;
  color: white;
}

.teamClass__block input {
  visibility: hidden;
}

.teamClass__block {
  width: fit-content;
  margin-right: 40px;
}

.teamClass__block span {
  display: inline-block;
  margin-left: 44px;
  color: #777;
}

.teamClass__block input:checked + label::before {
  display: block;
  content: "";
  text-align: center;
  font-size: 16px;
  background: lightgray;
  border: 3px solid lightgray;
}

.planTopic .themeColor {
  font-size: 18px;
  margin: -4px 0;
}

.topline {
  position: relative;
  margin-top: 15px;
  &::before {
    content: "";
    width: 1440px;
    height: 2px;
    top: -10px;
    background: #66cdb6;
    position: absolute;
    left: -100px;
  }
}

td {
  vertical-align: top;
}
