




































































































































































































































































.subTitleContainer {
  background: #66cdb6;
  color: #fff;
  border-radius: 10px;
  padding: 10px;
}

.active {
  color: #66cdb6;
}

.activeCircle {
  border-radius: 100%;
  width: 20px;
  height: 20px;
  border: 4px solid #eaeaea;
  background: #66cdb6;
  display: inline-block;
  vertical-align: middle;
}

.stepList {
  &:before {
    content: "";
    position: absolute;
    left: 14px;
    width: 4px;
    background: #d6d6d6;
    height: calc(100% + 35px);
  }
  &:last-of-type {
    &:before {
      content: "";
      position: absolute;
      left: 14px;
      width: 0;
      height: 0 !important;
    }
  }
}

.infoSetting {
  background: #66cdb6;
  border-radius: 10px;
  color: #fff;
  text-align: center;
  padding: 5px 0;
}
