























































.modal {
  position: fixed;
  background: #fff;
  width: 916px;
  overflow-y: scroll;
  border-bottom: 10px solid #66cdb6;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  z-index: 99999;
  left: 50%;
  transform: translateX(-50%);
  position: fixed;
  top: 5%;
  max-height: 90%;
  height: auto;
  header {
    background: #66cdb6;
    color: #fff;
    padding: 12px 12px;
    text-align: left;
    font-size: 1.5rem;
  }

  .content {
    padding: 12px 54px;
  }

  footer {
    width: 40%;
    text-align: center;
    background: #66cdb6;
    margin: auto;
    padding: 12px;
    color: #fff;
    margin-bottom: 12px;
    margin-bottom: 36px;
    border-radius: 4px;
  }
}

.modal::-webkit-scrollbar {
  display: none;
}

.whitedrop {
  position: absolute;
  background: #fff;
  opacity: 0.8;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100vw;
  height: 100vh;
}

.modalContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.listCss {
  top: 50% !important;
  transform: translate(-50%, -50%) !important;
  width: 525px !important;
}
